import { Container, Grid, Typography } from "@material-ui/core";
import React from "react";
import { useSelector } from "react-redux";

const SearchList = () => {
  const lists = useSelector((state) => state.films);
  return (
    <Container>
      {lists.searchFilms.length !== 0 ? (
        <div>
          <h1 style={{ color: "white" }}>Search Results: </h1>
          <Grid container spacing={2}>
            {lists.searchFilms.map((item) => {
              return (
                <Grid
                  item
                  xs={12}
                  lg={2}
                  key={item.id}
                  style={{ margin: "20px 0px" }}
                >
                  <img
                    src={item.images}
                    style={{ width: "168px", height: "223.39px" }}
                    alt={item.Title}
                  />
                  <Typography
                    onClick={() => this.setState({ open: true, id: item.id })}
                    variant="h5"
                    style={{
                      color: "#E35708",
                      fontSize: 15,
                      fontWeight: 600,
                      marginTop: 10,
                    }}
                  >
                    {item.titlesVN}
                  </Typography>
                </Grid>
              );
            })}
          </Grid>
        </div>
      ) : (
        <p>No data</p>
      )}
    </Container>
  );
};

export default SearchList;
